import React, { useState, useEffect } from 'react';


function GeneralData ({pageNames, page}){

let saveName = JSON.parse(window.localStorage.getItem("name"));
let saveEmail = JSON.parse(window.localStorage.getItem("email"));
let saveArea = JSON.parse(window.sessionStorage.getItem("area"));
let saveTask = JSON.parse(window.sessionStorage.getItem("task"));


  const saveBasicData = () => {
    const name = document.getElementById('name').value;
    const email = document.getElementById('email').value;
    const area = document.getElementById('area').value;
    const task = document.getElementById('task').value;

    window.localStorage.setItem("name", JSON.stringify(name));
    window.localStorage.setItem("email", JSON.stringify(email));
    window.sessionStorage.setItem("area", JSON.stringify(area));
    window.sessionStorage.setItem("task", JSON.stringify(task));


      
  };

  


    if (page === pageNames[0]){
    return(
        <div className=" body2 ">
        <div  className=" ">
        <div  className="form2">
        <div hidden={true} className="title2">Basic Info</div>
       
        <div  className="input-container2 ic22">
          <input defaultValue={saveName} autoComplete="new-password" id="name" className="input2" type="text" placeholder=" " />
          <div  className="cut2"></div>
          <label autoComplete="new-password" for="Name" className="placeholder2">Name</label>
        </div>
        <div  className="input-container2 ic22">
          <input defaultValue={saveEmail} autoComplete="new-password" id="email" className="input2" type="text" placeholder=" " />
          <div  className="cut2 "></div>
          <label autoComplete="new-password" for="email" className="placeholder2">Email</label>
        </div>
        <div  className="input-container2 ic22">
          <input defaultValue={saveArea} autoComplete="new-password" id="area" className="input2" type="text" placeholder=" " />
          <div  className="cut2 cut-short2"></div>
                <label autoComplete="new-password" for="area" className="placeholder2">Area</label>
        </div>
         <div  className="input-container2 ic22">
          <input defaultValue={saveTask} autoComplete="new-password" id="task" className="input2" type="text" placeholder=" " />
          <div  className="cut2 cut-short2"></div>
          <label autoComplete="new-password" for="task" className="placeholder2">Task</label>
        </div>
        
        <button type="text" className="submit2"  onClick={saveBasicData} >submit</button> 
    
      </div>
      </div>
      </div>
      );
    
}
}


export default GeneralData;