import React, { useState, useEffect } from 'react';
import Accordion from 'react-bootstrap/Accordion';
import { useAccordionButton } from 'react-bootstrap/AccordionButton';
import Card from 'react-bootstrap/Card';
import HazardDrop from './HazardDropDown/HazardDropDown';

function Hazard(props) {

const [rerenderPage, setRerenderPage] = useState(0)

  const resetHazards = () =>{
  let i = 1;
  while (i < 7) {
    window.sessionStorage.removeItem(("Hazard"+i))
    window.sessionStorage.removeItem(("Risk"+i))
    window.sessionStorage.removeItem(("Control"+i));
    i++;
  }
  setRerenderPage(rerenderPage+1);
};

function CustomToggle({ children, eventKey }) {
  const decoratedOnClick = useAccordionButton(eventKey, () =>{

});

  return (
    <button 
      type="button"
      style={{ backgroundColor: 'pink' }}
      onClick={decoratedOnClick}
    >
      {children}
    </button>
  );
}

  if (props.page === props.pageNames[1]){
    return (
    <div>
    <Accordion key={rerenderPage}>
       <Card>
        <Card.Header>
          <CustomToggle eventKey="0">Add Hazard</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="0" >
          <Card.Body>      
            <div>
            <HazardDrop hazNum="1"/>
            </div>          
            </Card.Body>
        </Accordion.Collapse>
      </Card>
      <Card>
        <Card.Header>
          <CustomToggle eventKey="1">Add Hazard</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="1">
          <Card.Body>      
            <div>
            <HazardDrop hazNum="2" />
            </div>          
            </Card.Body>
        </Accordion.Collapse>
      </Card>
      <Card>
        <Card.Header>
          <CustomToggle eventKey="2">Add Hazard</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="2">
          <Card.Body>      
            <div>
            <HazardDrop hazNum="3"/>
            </div>          
            </Card.Body>
        </Accordion.Collapse>
      </Card>
      <Card>
        <Card.Header>
          <CustomToggle eventKey="3">Add Hazard</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="3">
          <Card.Body>      
            <div>
            <HazardDrop hazNum="3"/>
            </div>          
            </Card.Body>
        </Accordion.Collapse>
      </Card>
      <Card>
        <Card.Header>
          <CustomToggle eventKey="4">Add Hazard</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="4">
          <Card.Body>      
            <div>
            <HazardDrop hazNum="4"/>
            </div>          
            </Card.Body>
        </Accordion.Collapse>
      </Card>
      <Card>
        <Card.Header>
          <CustomToggle eventKey="5">Add Hazard</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="5">
          <Card.Body>      
            <div>
            <HazardDrop hazNum="5"/>
            </div>          
            </Card.Body>
        </Accordion.Collapse>
      </Card>
      <Card>
        <Card.Header>
          <CustomToggle eventKey="6">Add Hazard</CustomToggle>
        </Card.Header>
        <Accordion.Collapse eventKey="6">
          <Card.Body>      
            <div>
            <HazardDrop hazNum="6"/>
            </div>          
            </Card.Body>
        </Accordion.Collapse>
      </Card>
    </Accordion>
    <button onClick={resetHazards} className="submit2">Reset Hazards</button>
    </div>
  );}
}



export default Hazard