import React, { useState, useEffect } from 'react';

function AddNote ({pageNames, page, width}){

let saveNote = JSON.parse(window.sessionStorage.getItem("note"));

  const saveBasicData = () => {
    const note = document.getElementById('note').value;
    window.sessionStorage.setItem("note", JSON.stringify(note));
  };

    if (page === pageNames[2]){
    return(
        <div className=" body3 form4">
        <div  className=" form4 ">
        <div  className="form4">   
        <div  className="input-container2 ic22 form4">
          <textarea maxlength="168" defaultValue={saveNote} autoComplete="new-password" id="note" className="input2 input3 form4" type="text" placeholder=" " />
          <div  className="cut2 cut-short2"></div>
          <label autoComplete="new-password" for="Note" className="placeholder2">Note</label>
        </div>      
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <button type="text" className="submit2"  onClick={saveBasicData} >submit</button> 
    

      </div>
      </div>
      </div>
      );
    
}
}


export default AddNote;