import './App.css';
import React, { useState, useEffect} from 'react';
import UploadAndDisplayImage from './components/UploadImage/UploadImage';
import Hazard from './components/Hazard';
import OffCanvas from "./components/OffCanvas"
import Header from './components/Header';
// import AddCom from './components/AddCom/AddCom';
// import ExportPdf from './components/ReactPdf/ReactPdf';
import ExportPdf from './components/ReactPdf/ReactPdfKendo';
import GeneralData from './components/GeneralData/GeneralData';
import AddNote from './components/AddNote/AddNote';
import HazardDrop from './components/HazardDropDown/HazardDropDown';
import "react-widgets/styles.css";


//allows for naming of pages in the headers, all logic involving the headers uses this array 
//so it is safe to change the names here. Use single words only, no spaces.
const pageNames = ["Basic-Info", "Hazards", "Note", "Image", "Export-PDF", "disabled"];

function App () {
  
//React Hook to get the screen width when it is resized or when loaded. 
const [scrWidth, setWindowSize] = useState(getWindowSize);


function getWindowSize() {
  const {innerWidth} = window;
  return {innerWidth};
  
}

useEffect(() => {
  function handleWindowResize() {
    setWindowSize(getWindowSize());
  }

  window.addEventListener('resize', handleWindowResize);

  return () => {
    window.removeEventListener('resize', handleWindowResize);
  };
}, []);
//END OF - React Hook to get the screen width when it is resized or when loaded.

//getting trigger from header button from header child
const [data, setData] = useState('');

const childToParent = (childdata) => {
  setData(childdata);
}

// hook for setting components based on value in href
const [page, setPage] = useState(getPageName)


function getPageName() {
  let pageName = pageNames[0];
  
  const hrefPage = (value) => {
    return(window.location.href.endsWith(value));}

  if (hrefPage(pageNames[1]) === true){
    pageName = pageNames[1]
  } else if (hrefPage(pageNames[2]) === true){
    pageName = pageNames[2]
  } else if (hrefPage(pageNames[3]) === true){
    pageName = pageNames[3]
  } else if (hrefPage(pageNames[4]) === true){
    pageName = pageNames[4]
  } else {
    pageName = pageNames[0]
  }
  return {pageName};   
}

useEffect(() => {
  function handleHrefChange() {
    setPage(getPageName());
  }

  window.addEventListener('popstate', handleHrefChange);

  return () => {
    window.removeEventListener('popstate', handleHrefChange);
  };
}, []);
// end of hook for setting components based on value in href

  return (
    
    <div className='test'>
 
      <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no" />
      <Header page={ page.pageName } pageNames={ pageNames } childToParent={childToParent}/>
      <GeneralData  page={ page.pageName } pageNames={ pageNames }/>
      
      <Hazard page={ page.pageName } pageNames={ pageNames }/>
     
      {/* <OffCanvas page={ page.pageName } pageNames={ pageNames }/> */}
      {/* <AddCom page={ page.pageName } pageNames={ pageNames }/> */}
      <div className="Picture">
      <UploadAndDisplayImage width={ scrWidth.innerWidth } page={ page.pageName } pageNames={ pageNames }/>
      <AddNote width={ scrWidth.innerWidth } page={ page.pageName } pageNames={ pageNames }/>
      </div>
      <ExportPdf width={ scrWidth.innerWidth } page={ page.pageName } pageNames={ pageNames }/>
      </div>
  );
}

export default App;
