import React, { useState, useEffect } from 'react';
import "./HazardDropDown.css"
import DropdownList from "react-widgets/DropdownList";

function HazardDrop ({pageNames, page, width, hazNum,}){
let initialHazards = [];
let initialRisks = [];
let initialControls = [];

if (window.localStorage.getItem("hazards") === null){
  initialHazards = [`Manual Handling / Pushing`, `Tools / equipment / machinery`, 
  `Release of stored energy`, 
  `Work at height`,
  `Confined spaces`,
  `chemicals / fluids / oils`,
  `electricity low voltage`,
  `dust and fumes`,
  `noise / vibrations`,
  `contact with machinery or process`,  
  `lighting`,
  `housekeeping`,
  `lifting operations`,
  `hot work`,
  `hazardous atmosphere`,
  `insulation`,
  `excavation / created opening`,
  `weather`,
  `environmental conditions`,
  `mobile equipment`,]
  }else {
    initialHazards = JSON.parse(window.localStorage.getItem("hazards"));
};

if (window.localStorage.getItem("risks") === null){
  initialRisks = [`slips / trips / sprains / strains`,
  `cuts movement / struck / sprains / trapped / entanglement`,
  `discharge / spring / spray  / strike / scold`,
  `people / articles / falling / damage / plant`,
  `trapped / falls / unconsciousness`,
  `burns / scolding / leak / freeze burns / fire / blindness`,
  `shock / burns / unexpected starting`,
  `fire / inhalation / blindness / damage / health`,
  `hearing loss / temporary or permanent`,
  `scolding / burns / cuts / pinching /entanglement`,
  `blind spots / shadows / tiredness / blindness/`,
  `slips / trips / falls`,
  `falling objects`,
  `sparks / burns / fume / fire`,
  `trapped personnel / fire / explosives / toxic`,
  `irritation / cuts / inhalation of fibres`,
  `falls / collapses / unconsciousness`,
  `slips / trips / falls / tools / equipment`,
  `concentration / fainting / coordination`,
  `strike / fall over / hit personnel`,]
  }else {
    initialRisks = JSON.parse(window.localStorage.getItem("risks"));
};

if (window.localStorage.getItem("controls") === null){
  initialControls = [`avoid / correct lifting techniques/ mechanical aides`,
  `Good condition, competent in use, good quality tools`,
  `Correct isolation / controlled release`,
  `Guard rails / fall arrest / lanyards / barrier area`,
  `valid permits / correct procedures / safety standby`,
  `correct isolation / collection trays / PPE`,
  `correct isolation / checking for dead`,
  `information / LEV / PPE / RPE`,
  `wear hearing protection / use correct tools`,
  `check equipment`,
  `temporary lighting`,
  `clean up before and after completing task`,
  `check area / barriers / communication`,
  `correct permit / extinguisher availability`,
  `escape routes / harness / life saving equipment`,
  `PPE / RPE / area isolation / housekeeping`,
  `excavation permits / barriers / weather`,
  `3 point contact on stairs / ladders / PPE`,
  `procedure for hot / cold working / breaks / safety standby`,
  `banksman / hi-vis vest / competent user`,]
  }else {
    initialControls = JSON.parse(window.localStorage.getItem("controls"));
};

let hazardPlaceholder;
let riskPlaceholder;
let controlPlaceholder;

if (window.sessionStorage.getItem("Hazard"+hazNum) === null){
  hazardPlaceholder = "Please select a hazard";
  console.log("Hazard"+hazNum);
  }else {
    hazardPlaceholder = JSON.parse(window.sessionStorage.getItem("Hazard"+hazNum));
};

if (window.sessionStorage.getItem("Risk"+hazNum) === null){
  riskPlaceholder = "Please select a risk";
  console.log("Risk"+hazNum);
  }else {
    riskPlaceholder = JSON.parse(window.sessionStorage.getItem("Risk"+hazNum));
};

if (window.sessionStorage.getItem("Control"+hazNum) === null){
  controlPlaceholder = "Please select a control";
  console.log("Control"+hazNum);
  }else {
    controlPlaceholder = JSON.parse(window.sessionStorage.getItem("Control"+hazNum));
};


//allows list changes to be loaded accross all lists with rerendering which would otherwise delete previous entries
const loadListHazards = () =>{
  if (openHazard === false) {
    setHazards(JSON.parse(window.localStorage.getItem("hazards")))};
};

const loadListRisks = () =>{
  if (openRisk === false) {
    setRisks(JSON.parse(window.localStorage.getItem("risks")))};
};

const loadListControls = () =>{
  if (openControl === false) {
    setControls(JSON.parse(window.localStorage.getItem("controls")))};
};


    const [openHazard, setOpenHazard] = useState(false);
    const [openRisk, setOpenRisk] = useState(false);
    const [openControl, setOpenControl] = useState(false);
    const [valueHazard, setValueHazard] = useState(hazardPlaceholder);
    const [valueRisk, setValueRisk] = useState(riskPlaceholder);
    const [valueControl, setValueControl] = useState(controlPlaceholder);
    const [hazards, setHazards] = useState(initialHazards);
    const [risks, setRisks] = useState(initialRisks);
    const [controls, setControls] = useState(initialControls);
 
//create new list items
function handleCreateHazard(name) {
    let newOption = {name}
    // select new option
    setValueHazard(newOption.name)
    // add new option to our dataset
    setHazards([newOption.name, ...hazards]);
    setOpenHazard(false);
  }
  function handleCreateRisk(name) {
    let newOption = {name}
    // select new option
    setValueRisk(newOption.name)
    // add new option to our dataset
    setRisks([newOption.name, ...risks]);
    setOpenRisk(false);
  }
  function handleCreateControl(name) {
    let newOption = {name}
    // select new option
    setValueControl(newOption.name)
    // add new option to our dataset
    setControls([newOption.name, ...controls]);
    setOpenControl(false);
  }

//storing array into local storage
  window.localStorage.setItem("hazards", JSON.stringify(hazards));
  window.localStorage.setItem("risks", JSON.stringify(risks));
  window.localStorage.setItem("controls", JSON.stringify(controls));
//storing of selections into session storage
  const storeValue = (key, value) =>{
    window.sessionStorage.setItem(key, JSON.stringify(value));
    };

    if (valueHazard != hazardPlaceholder){
      storeValue("Hazard"+hazNum, valueHazard); 
    };

    if (valueRisk != riskPlaceholder){
      storeValue("Risk"+hazNum, valueRisk); 
    };

    if (valueControl != controlPlaceholder){
      storeValue("Control"+hazNum, valueControl); 
    };

    return( 
     <div>
       <div>
       <DropdownList 
          onClick={() => setOpenHazard(prev => !prev)}
          onKeyPress={() => setOpenHazard(true)}
          onMouseLeave={() => setOpenHazard(false)}      
          open={openHazard}
          data={hazards}
          onToggle={loadListHazards}
          allowCreate="onFilter"
          onCreate={handleCreateHazard}
          defaultValue="Hazards"
          value={valueHazard}
          onChange={value => {setValueHazard(value)}}
          onInput={() => setOpenHazard(true)}
          filter='contains'    
          onSelect={() => setOpenHazard(false)} 
            />
          </div> 
          <br/>
          <div>
       <DropdownList 
          onClick={() => setOpenRisk(prev => !prev)}
          onKeyPress={() => setOpenRisk(true)}
          onMouseLeave={() => setOpenRisk(false)}
          open={openRisk}
          data={risks}
          onToggle={loadListRisks}
          allowCreate="onFilter"
          onCreate={handleCreateRisk}
          defaultValue="Risk"
          value={valueRisk}
          onChange={value => {setValueRisk(value)}}
          onInput={() => setOpenRisk(true)}     
          filter='contains'
          onSelect={() => setOpenRisk(false)} 
          />
          </div> 
          <br/>
          <div>
       <DropdownList 
          onClick={() => setOpenControl(prev => !prev)}
          onKeyPress={() => setOpenControl(true)}
          onMouseLeave={() => setOpenControl(false)}
          open={openControl}
          data={controls}
          onToggle={loadListControls}
          allowCreate="onFilter"
          onCreate={handleCreateControl}
          defaultValue="Control"
          value={valueControl}
          onChange={value => {setValueControl(value)}}
          onInput={() => setOpenControl(true)}     
          filter='contains'
          onSelect={() => setOpenControl(false)} 
          />
          </div>        
     </div>
      )
};
export default HazardDrop;