import Card from 'react-bootstrap/Card';
import Nav from 'react-bootstrap/Nav';
import "./Header.css"



function Header({pageNames, page, childToParent}) {
const data = "This is data from Child Component to the Parent Component."

let cardText;
let cardTitle;
if (page === pageNames[0] ){
  cardTitle = "Please enter basic information about yourself and the task";
  cardText = "test";
} else if (page === pageNames[1]){
  cardTitle = "The " + page + " page has been selected";
  cardText = "nope"
} else if (page === pageNames[2]){
  cardTitle = "Please submit some additional information if required";
  cardText = "dope"
} else if (page === pageNames[3]){
  cardTitle = "Please upload an image if required";
  cardText = "Please upload an image if required"
} else if (page === pageNames[4]){
  cardTitle = "The " + page + " page has been selected";
  cardText = "final"
};

  return (
    <Card>
      <Card.Header className="CardHeader">
        <Nav variant="pills" defaultActiveKey= {"#" + page}>
          <Nav.Item>
            <Nav.Link href={"#" + pageNames[0]}>{pageNames[0]}</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link  href={"#" + pageNames[1]}>{pageNames[1]}</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href={"#" + pageNames[2]}>{pageNames[2]}</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link href={"#" + pageNames[3]}>{pageNames[3]}</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link onClick={() => childToParent(data)} href={"#" + pageNames[4]}>{pageNames[4]}</Nav.Link>
          </Nav.Item>
          {/* <Nav.Item>
            <Nav.Link href={"#" + pageNames[5]} disabled>{pageNames[5]}</Nav.Link>
          </Nav.Item> */}
        </Nav>
      </Card.Header>
      <Card.Body>
        <Card.Title>{cardTitle}</Card.Title>
        <Card.Text hidden={true}>
        {cardText}
        </Card.Text>
      </Card.Body>  
    </Card>
  );
}

export default Header;