import React from 'react';

function RenderHazard ({hazNum}) {

let hazard = JSON.parse(window.sessionStorage.getItem("Hazard"+hazNum));
let risk = JSON.parse(window.sessionStorage.getItem("Risk"+hazNum));
let control = JSON.parse(window.sessionStorage.getItem("Control"+hazNum));

let hazardText;
let riskText;
let controlText

if (JSON.parse(window.sessionStorage.getItem("Hazard"+hazNum)) != null) {
   hazardText = "Hazard";
   riskText = "Risk";
   controlText = "Control";
} else {
   hazard = null;
   risk = null;
   control = null;
};
     
 return (
   <div>
      <div className='pdfHazTitleDiv'>
      <label className='pdfHazTitle'>{hazardText}</label>
      <label className='pdfRiskTitle'>{riskText}</label>
      <label className='pdfContTitle'>{controlText}</label>
      <label className='pdfHazText'>{hazard}</label>
      <label className='pdfRiskText'>{risk}</label>
      <label className='pdfContText '>{control}</label>
      </div>
   <span  className="brxsmall"></span>
   </div>
     );
}

export default RenderHazard  