import React, { useState, useEffect } from 'react';
import * as ReactDOM from "react-dom";
import { PDFExport } from "@progress/kendo-react-pdf";
import "./ReactPdf.css"
import placeholder from "./placeholder.jpg"
import {RemoveScroll} from 'react-remove-scroll';
import RenderHazard from "./renderHazard/renderHazard"

let utc = new Date().toLocaleDateString()
let today = new Date();
let time = today.getHours() + ":" + today.getMinutes();

// let uploadedUrl = "";
// function testit(){
//     uploadedUrl = JSON.parse(window.sessionStorage.getItem("testsiess")) ;
//     console.log(uploadedUrl);
// };


const ExportPdf = (props) => {
//hook declerations
const [uploadedUrl, setUploadedUrl] = useState("")
const [basicDataName, setBasicDataName] = useState("")
const [basicDataEmail, setBasicDataEmail] = useState("")
const [basicDataArea, setBasicDataArea] = useState("")
const [basicDataTask, setBasicDataTask] = useState("")
const [addNote, setaddNote] = useState("")

const loadPage = () =>{
setUploadedUrl(JSON.parse(window.sessionStorage.getItem("testsiess"))) ;
setBasicDataName(JSON.parse(window.localStorage.getItem("name"))) ;
setBasicDataEmail(JSON.parse(window.localStorage.getItem("email"))) ;
setBasicDataArea(JSON.parse(window.sessionStorage.getItem("area"))) ;
setBasicDataTask(JSON.parse(window.sessionStorage.getItem("task"))) ;
setaddNote(JSON.parse(window.sessionStorage.getItem("note"))  ) ;
const loadPDF = () => {
  if (pdfExportComponent.current) {
    pdfExportComponent.current.save();
  }}
setTimeout(loadPDF, 500);
};


let image;
if (uploadedUrl === "placeholder") {
    image = placeholder
} else {
    image = uploadedUrl
};




// function happy123(){
//     console.log("hello123")
// }
// document.getElementById("myFrame").addEventListener("load", happy123);

  const pdfExportComponent = React.useRef(null);
  if (props.page === props.pageNames[4]){
  return (
    <RemoveScroll>
      <button onClick={loadPage} className="submit2">Download PDF</button>
    <div className="Picture">
       
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
      <br/>
      <div>
      <PDFExport paperSize="A4" margin="1cm" ref={pdfExportComponent}>
       <div className="PDFbody">
          <div className="pdfHeaderDiv">
            <label className="pdfHeader pdfTitle">Point Of Work Risk Assessment</label>
            <label className="pdfHeader pdfDate">{utc + " - " + time}</label>
          </div>
          <div>
            <label className="pdfName">Name: {basicDataName}</label>       
            <label className="pdfName">Email: {basicDataEmail}</label>
          </div>
          <div>
            <label className="pdfName">Area: {basicDataArea}</label>
            <label className="pdfName">Task: {basicDataTask}</label>
          <div className="pdfNoteDiv">
          <label className="pdfNoteTitle">Note:</label>
          <label className="pdfNoteText">{addNote}</label>
          </div>
          <div>
          <RenderHazard hazNum={1}/>
          </div>
          <div>
          <RenderHazard hazNum={2}/>
          </div>
          <div>
          <RenderHazard hazNum={3}/>
          </div>
          <div>
          <RenderHazard hazNum={4}/>
          </div>
          <div>
          <RenderHazard hazNum={5}/>
          </div>
          <div>
          <RenderHazard hazNum={6}/>
          </div>
          <div>
          <RenderHazard hazNum={7}/>
          </div>
          <div className='imageDiv'>
            <img src={image} className="image" alt="Logo" />
          </div>
          </div>   
        </div>
      </PDFExport>
      </div>
  </div>  
  
  </RemoveScroll>
  );
};
}



export default ExportPdf;