import React, { useState } from "react";
import "./UploadImage.css";

//declerations of hooks
const UploadAndDisplayImage = (props) => {
  let [selectedImage, setSelectedImage] = useState(null);
  let [buttonText, setButtonText] = useState ("Upload Image");



//sending uploaded photo URL to session storage
let uploadURL;
if (selectedImage != null){
  uploadURL = window.URL.createObjectURL(selectedImage);
  window.sessionStorage.setItem("testsiess", JSON.stringify(uploadURL));
} else {window.sessionStorage.setItem("testsiess", JSON.stringify("placeholder"));};
 

//hook function for removing the image from the page
const removeImage = () => { 
  setSelectedImage(null);
  document.getElementById("fileInput").value = null;
  setButtonText("Upload Image");
};


const fileInputButton = () => {
  var button = document.getElementById('fileInput');
   button.click();
}

  if (props.page === props.pageNames[3]){
  return (
    <div className="centre ">
      <div className="form3">
      <button className="submit2 " onClick={fileInputButton}>{buttonText}</button>
      <input
        hidden
        id="fileInput"
        type="file"
        name="myImage"
        onChange={(event) => {
          setSelectedImage(event.target.files[0]);
          {setButtonText("Change Image")}
        }}
      />
      </div>
      {selectedImage && (
        <div className="centre">
        <div className="form3">
        <button className="submit2" onClick={ removeImage }>Remove Image</button>
        </div>
        <br/>
        <div>
        <img alt="Please upload a valid image file" width={props.width / 1.75} src={URL.createObjectURL(selectedImage)} />
        </div>
        </div>
      )}
    </div>
  );
};
}

export default UploadAndDisplayImage;